* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    text-decoration: none;
}

.mainContainer:hover {
    box-shadow: 1px 1px 5px 2px;
    transform: scale(1.1);
    
}

.card {
    border-radius: 5%;
    background-color: cornsilk;
    height: 240px;
    width: 240px;
    transform-style: preserve-3d;
    transition: all 0.5s ease;
    text-decoration: none;
}

.text {
    font-family: 'Open sans', sans-serif;
    font-size: 20px;
    line-height: 20px;
    font-weight: 400px;
    color: rgb(14, 13, 13);
}

.image {
    padding-top: 10px;
    border-radius: 10%;
    width: 220px;
    height: 170px;
}

.card_content {
    padding: 10px;
}

