.buttons {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.button {
    border-radius: 20%;
    height: 25px;
    width: 25px;
    margin: 8px;
    border-color: rgba(26, 188, 156, 0.8);
    color: #fff;
    box-shadow: 0 0 40px 40px rgba(26, 188, 156, 0.8) inset, 0 0 0 0 rgba(26, 188, 156, 0.6);
    -webkit-transition: all 150ms ease-in-out;
    transition: all 150ms ease-in-out;
}


.button:hover {
    color: rgba(9, 63, 52);
  box-shadow: 0 0 10px 0 rgba(26, 188, 156, 0.6) inset, 0 0 10px 4px rgba(26, 188, 156, 0.6);
}

