body {
    background: radial-gradient(#C2FFD9,#50CB93);
  
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    }

section {
    position: relative;
    width: 100%;
    height: 100vh;
    background: radial-gradient(#C2FFD9,#50CB93);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
section h1 {
    color: #03638D;
    font-size: 2cm;
    font-family: Comic Sans MS, Comic Sans, cursive
}

.set {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
}

section div {
    position: absolute;
    display: block;
}
.bone1 {
left: 20%;
animation: animate 15s linear infinite;
animation-delay: -7s;
}
.bone2 {
    left: 50%;
    animation: animate 20s linear infinite;
    animation-delay: -5s;
    }
.bone3 {
    left: 70%;
    animation: animate 20s linear infinite;
    animation-delay: 0s;
    }
.bone4 {
    left: 0%;
    animation: animate 15s linear infinite;
    animation-delay: -5s;
    }
.bone5 {
    left: 85%;
    animation: animate 18s linear infinite;
    animation-delay: -10s;
    }
.bone6 {
    left: 0%;
    animation: animate 12s linear infinite;
    }
.bone7 {
    left: 15%;
    animation: animate 14s linear infinite;
    }
.bone8 {
    left: 60%;
    animation: animate 15s linear infinite;
    }
@keyframes animate {
    0%
    {
        opacity: 0;
        top: -10%;
        transform: translateX(20px) rotate(0deg);
    }
    10%
    {
        opacity: 1;
    }
    20%
    {
        transform: translateX(-20px) rotate(45deg);
    }
    40%
    {
        transform: translateX(-20px) rotate(90deg);
    }
    60%
    {
        transform: translateX(20px) rotate(180deg);
    }
    80%
    {
        transform: translateX(-20px) rotate(180deg);
    }
    100%
    {
        top:110%;
        transform: translateX(-20px) rotate(225deg);
    }

}


.set2 {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    transform: scale(2) rotateY(180deg);
    filter: blur(2px);
}
.set3 {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    transform: scale(0.7) rotateX(180deg);
    filter: blur(2px);
}

.dogruning {
    position:fixed;
    width: 20%;
    height: 20%;
    bottom: 15px;
    left: 400px;
}
.doghome {
    position:fixed;
    bottom: 15px;
    right: 100px;
}
.buttonhome {
    position:fixed;
    bottom: 148px;
    right: 165px;
}
.btn {
    color: whitesmoke;
    width: 60px;
    background-color: rgb(175, 25, 25);
    border-color: black;
    border-radius: 30%;
    margin-top: 5px;
    align-self: flex-end;
    margin-right: 15px;
    margin-bottom: 20px;


}

.buttonhome:hover {
    background-color: #FFF47D;
    border-radius: 10px;
    width: 102px;
}